<template>
  <div class="signlifeprint">
    <!-- 畫面顯示內容區域 -->
    <!-- <h3>{{cData.company_name}}</h3>

    <div class="text-center border-bottom my-2">
      <h1>
        <strong>生命徵象紀錄單</strong>
      </h1>
    </div> -->

    <table class="form_title" border="2px #000000 solid;" width="100%">
      <thead>
        <tr>
              <td colspan="18">
                <v-row>
                  <v-col cols="3">
                    <h3>{{ cData.company_name }}</h3>
                  </v-col>
                  <v-col cols="6" class="text-center">
                    <h1>
                      <strong>生命徵象紀錄單</strong>
                    </h1>
                  </v-col>
                  <v-col cols="3"></v-col>
                </v-row>
              </td>
            </tr>
        <tr>
          <th colspan="18" class="p-2">
            <v-row>
              <v-col cols="2">病歷號: {{uData.p_code}}</v-col>
              <v-col cols="2">床號: {{uData.bed_name}}</v-col>
              <v-col cols="2">姓名: {{uData.name}}</v-col>
              <v-col cols="2">性別: {{uData.sex}}</v-col>
              <v-col cols="2">生日: {{uData.birthday}}</v-col>
              <v-col cols="2">年齡: {{age}}</v-col>
              <!-- <v-col cols="12">疾病史: {{uData.hadadisease}}</v-col> -->
            </v-row>
          </th>
        </tr>
        <tr class="text-center font-weight-bold text-nowrap">
          <td>日期</td>
          <td>時間</td>
          <th>血壓(H)</th>
          <th>血壓(L)</th>
          <th>脈搏</th>
          <th>體溫</th>
          <th>呼吸</th>
          <th>血糖</th>
          <th>血氧濃度</th>
          <th>疼痛指數</th>
          <th>GCS-E</th>
          <th>GCS-V</th>
          <th>GCS-M</th>
          <th>大便次數</th>
          <th>體重</th>
          <th>備註</th>
          <th>紀錄人</th>
          <th>蓋章</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="(item,index) in items" :key="index">
          <td>{{item.date}}</td>
          <td>{{item.time}}</td>
          <td>{{item.bp_h}}</td>
          <td>{{item.bp_l}}</td>
          <td>{{item.pulse}}</td>
          <td>{{item.temperature}}</td>
          <td>{{item.breath}}</td>
          <td>{{item.blood_sugar}}</td>
          <td>{{item.blood_oxygen}}</td>
          <td>{{item.pain}}</td>
          <td>{{item.gcs_e}}</td>
          <td>{{item.gcs_v}}</td>
          <td>{{item.gcs_m}}</td>
          <td>{{item.stool}}</td>
          <td>{{item.weight}}</td>
          <td>{{item.note}}</td>
          <td>{{item.create_man | filterName}}</td>
          <td width="5%"></td>
        </tr>
      </tbody>
    </table>

    <!-- <pre>{{items}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items: [],
      // nursingassessmentItems: {},
      // nutritionItems: [],
      // recordItems: []
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    //2020.9.20 加入判斷同日的時間排序
    this.items = JSON.parse(sessionStorage.getItem("printData")).sort(function(a,b){
      if (a.date == b.date){
        return a.time > b.time ? 1 : -1;
      }
    });

  },

  methods: {},
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.signlifeprint {
  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }

  td {
    padding: 4px;
    border: 1px #000000 solid;
  }
}
@media print {
  .signlifeprint {
    .form_title {
      font-size: 1rem;
      // text-align: center;
      font-family: "微軟正黑體";
    }

    td {
      padding: 4px;
      border: 1px #000000 solid;
    }

    .v-chip {
      font-size: 1rem;
    }

    @page {
      size: A4 portrait;
      // margin: 0cm;
      // padding: 0cm;
    }
  }
}
</style>